*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  color: #393C49;
}

body{
  width: 100%;
  overflow-x: hidden;
}

@keyframes animateDown {
  0%, 20%, 50%, 80%{
    transform: translateY(0);
  }
  40%{
    transform: translateY(5px);
  }

  60%{
    transform: translateY(3px);
  }

}

a{
  text-decoration: none;
}